import ServiceCard from "../components/ServiceCard";

export default function Services() {
  return (
    <section className="section">
      <h2>Our Services</h2>
      <div className="grid">
        <ServiceCard title="Education Management Systems"
          desc="School, college, university & library management solutions." />
        <ServiceCard title="HR & Payroll Solutions"
          desc="Employee management, payroll & attendance systems." />
        <ServiceCard title="Custom Software Development"
          desc="ERP, CRM, dashboards & custom platforms." />
        <ServiceCard title="Web & App Development"
          desc="Professional websites and mobile applications." />
        <ServiceCard title="Creator & Influencer Platforms"
          desc="Personal brand websites and tools." />
        <ServiceCard title="Business Automation"
          desc="Digital transformation for businesses." />
      </div>
    </section>
  );
}
