import { motion } from "framer-motion";

export default function Home() {
  return (
    <section className="min-h-[90vh] flex items-center px-6">
      <div className="max-w-6xl mx-auto grid md:grid-cols-2 gap-12 items-center">

        <motion.div
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
        >
          <h1 className="text-5xl font-extrabold">
            We Build <span className="text-primary">Digital Solutions</span>
            <br /> That Grow Businesses
          </h1>

          <p className="mt-6 text-gray-400">
            Skyreti EduCore helps schools, universities, companies,
            creators and enterprises build scalable digital systems.
          </p>

          <a
            href="/contact"
            className="inline-block mt-8 px-6 py-3 bg-primary text-black rounded-lg"
          >
            Work With Us
          </a>
        </motion.div>

        <motion.img
          src="/school-demo.png"
          className="rounded-xl shadow-2xl"
          initial={{ opacity: 0, scale: 0.95 }}
          whileInView={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.6 }}
        />
      </div>
    </section>
  );
}
