import WorkCard from "../components/WorkCard";

export default function Work() {
  return (
    <section className="px-6 py-20">
      <h2 className="text-4xl font-bold text-center mb-14">
        Selected Work
      </h2>

      <div className="max-w-6xl mx-auto grid sm:grid-cols-2 lg:grid-cols-3 gap-8">
        <WorkCard img="/school-demo.png" title="School Management System"
          desc="Student, attendance & fee management (concept UI)." />
        <WorkCard img="/college-demo.png" title="College ERP Platform"
          desc="Academic & administration dashboard." />
        <WorkCard img="/hr-demo.png" title="HR Payroll System"
          desc="Employee, salary & attendance automation." />
        <WorkCard img="/lms-demo.png" title="Learning Management System"
          desc="Courses, progress & analytics." />
      </div>
    </section>
  );
}
